.popup__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0;
  margin-inline-end: 0;
}

@media (max-width: 618px) {
  .popup__container {
    width: 282px;
    gap: 16px;
  }
}
